<template>  
    <div class="information_body">
        <div class="container">
            <div class="information">
                <div class="row information_height text-center justify-content-center align-items-center">
                    <div class="row container">
                        <!-- <h1 class="col-12 information__logo pb-2">Daily Music</h1> -->
                        <div class="col-sm-12 offset-lg-4 col-lg-4 pb-2 pt-2 pt-md-3 pb-md-3">
                            <div class="information__img" id="track-image-container">
                                <img id="track-image">
                                <div class="information__btn">
                                    <font-awesome-icon @click="addTrack()" icon="thumbs-up"/>
                                </div>
                            </div>
                        </div>
                        <div class="col-12 pb-2 pt-2 pt-md-3 pb-md-3">
                            <h3 class="information__title" id="track-name"></h3>
                        </div>
                        <div class="col-12">
                            <div class="information__numeric row justify-content-center pt-2">
                                <div class="col-3 information__numeric_currentTime"></div>
                                <div class="col-3 information__numeric_songLength"></div>
                            </div>
                            <input disabled class="information__currentTime" id="current-time" type="range" name="points" step="1" min="0"
                            value="0">
                        </div>
                    </div>
                </div> 
            </div>
        </div>
    </div>
</template>

<script>
import { accessToken, refreshTokens } from '../assets/js/userApi';
import Axios from 'axios';
export default {
    name: 'Information',
    props: ['songName'],
    methods: {
        addTrack(){
            Axios({
                method: 'post',
                url: 'https://dailyradio-server.herokuapp.com/favorites/add',
                headers: {
                    Authorization: 'Bearer ' + accessToken
                },
                data: {
                    song: this.songName,
                }
            }).then (res => {
            }).catch(error => { refreshTokens(this.addTrack()); });
        }
    },
}
</script>

<style lang="scss">
@import '../variables';
.information_height {
    height: 100vh;
}
.information {
    color: #324034;
    &__logo {
        font-size: 28px;

        @media screen and (min-width: 768px) {
        font-size: 40px;
        }
    }

    &__img img {
        border: 1px solid #071726;
        height: 250px;
        width: auto;
        box-shadow: 2px 2px 10px 4px rgba(0, 0, 0, 0.3);
        @media screen and (min-width: 768px) {
            height: 300px;
        }
    }
    &__img:hover img{
        filter: blur(2px);
    }

    &__btn {
        cursor: pointer;
        transform: translate(-50%, -50%);
        color: #324034;
        font-size: 32px;
        position: absolute;
        left: 50%;
        top: 50%;
        &:hover {
            color:#4C5939;
            img {
                filter: blur(1px);
            }
        }
    }

    &__title {
        height: 2em;
        margin: 0 auto;
        font-size: 16px;

        @media screen and (min-width: 768px) {
        font-size: 26px;
        }
    }

    &__numeric {
        font-size: 12px;

        @media screen and (min-height: 768px) {
        font-size: 24px;
        }
    }

    &__currentTime {
        outline: none;
        -webkit-appearance: none;
        background-color: #4C5939;
        width: 260px;
        height: 2px;
        border-radius: 15px;
    }
}
</style>