<template>
    <div id="radio">
        <Information :songName="playlist[songIndex]"/>
        <Player/>
    </div>
</template>

<script>
import {myAudio, myAudio2, musicUrl, firstStart} from '../assets/js/Audio'
import $ from 'jquery'
import Player from '../components/Player'
import Information from '../components/Information'
import Axios from 'axios'
import {accessToken, refreshTokens} from '../assets/js/userApi'
import { once } from 'events'
export default {
    name: 'radio',
    components: {
        Player,
        Information
    },
    data() {
        return {
            autoplay: false,
            playlist: [],
            songIndex: -1,
            songName: ''
        }
    },
    created() {
        fetch(musicUrl + '/playlist').then(res => {
            res.json().then(data => {
                this.playlist = data;
                this.GetNewSong(1);
            });
        });
    },
    mounted() {
        $(myAudio).on('durationchange', () => {
            $('.information__numeric_songLength').html(
                `${parseInt(myAudio.duration / 60)}:${this.PrettyTimeFormat(
                parseInt(myAudio.duration % 60)
                )}`
            );
        });

        $(myAudio).on('canplay', () => {
            if (this.autoplay) myAudio.play().catch();
        });

        let curtime;
        $(myAudio).on('timeupdate', () => {
            curtime = myAudio.currentTime;
            $('#current-time')
                .attr('value', curtime)
                .attr('max', myAudio.duration);
            $('.information__numeric_currentTime').html(
                `${parseInt(myAudio.currentTime / 60)}:${this.PrettyTimeFormat(
                parseInt(myAudio.currentTime % 60)
                )}`
            );
        });

        $(myAudio).on('ended', () => {
            this.GetNewSong(1);
        });

        $('#player-previous-button').on('click', () => {
            this.GetNewSong(-1);
            $('.information__numeric_songLength').html('');
        });
        $('#player-next-button').on('click', () => {
            this.GetNewSong(1);
            $('.information__numeric_songLength').html('');
        });

        $(myAudio).on('play', () => {
            this.flag = true;
            this.autoplay = true;
        });

        $(myAudio).on('pause', () => {
            this.flag = false;
            this.autoplay = false;
        });

        $('#play').on('click', () => {
            
            myAudio2.pause();
            if (this.flag) myAudio.pause();
            else myAudio.play();
        });
    },
     methods: {
        GetNewSong(step = 1) {
            if (this.songIndex + step >= this.playlist.length) this.songIndex = 0;
            else if (this.songIndex + step < 0) this.songIndex = 0;
            else this.songIndex += step;

            const songUrl = `${musicUrl}play/${this.playlist[this.songIndex]}/`;
            let title, artist, picture;

            fetch(songUrl + 'stats').then(async res => {
                let songStats = await res.json();
                title = songStats.title;
                artist = songStats.artist;
                picture = songStats.picture;

                title = title || 'Unknown title';
                artist = artist || 'Unknown artist';
                $('#track-name').html(title + ' - ' + artist);
                if (picture) {
                $('#track-image').prop('src', this._imageEncode(picture.data.data));
                } else {
                $('#track-image').prop('src', 'https://i.imgur.com/1Y5Uhk6.png');
                }

                this.songName = this.playlist[this.songIndex];
                myAudio.src = songUrl;
                myAudio.load();
            });
        },
        PrettyTimeFormat(num) {
            return num < 10 ? `0${num}` : num;
        },
        _imageEncode(arrayBuffer) {
            let b64encoded = btoa(
                [].reduce.call(
                new Uint8Array(arrayBuffer),
                (p, c) => {
                    return p + String.fromCharCode(c);
                },
                ''
                )
            );
            let mimetype = 'image/jpeg';
            return 'data:' + mimetype + ';base64,' + b64encoded;
        }
    }
}
</script>

